<template>
        <div class="edcution">
                <videoBanner></videoBanner>
                <div class="edcution-inner">
                        <!-- <p class="df">
                            <nuxt-link-locale class="link" to="/">
                                <span class="arrow"></span>
                                <span class="txt">{{ t("Battle.back") }}</span>
                            </nuxt-link-locale>
                        </p> -->
                        <div class="edcution-breadcrumb">
                                <publicBreadcrumb :item="item"></publicBreadcrumb>
                        </div>
                        <div class="edcution-search-line">
                                <div class="edcution-btns">
                                        <button
                                                class="btn"
                                                :class="{ acitve: activeTab === idx }"
                                                @click="triggerHandler(idx)"
                                                v-for="(item, idx) in ACADEMY_LIST"
                                                :key="idx"
                                                :lang="locale"
                                        >
                                                {{ t(item.label) }}
                                        </button>
                                </div>
                                <div class="edcution-search">
                                        <input
                                                class="in"
                                                v-model="inputVal"
                                                @input="searchHandler"
                                                :placeholder="t('Login.placeholder6')"
                                        />
                                        <i class="iconfont icon-sousuo"></i>
                                </div>
                        </div>
                        <div class="edcution-videos" v-if="vList?.length > 0">
                                <div class="item" v-for="(val, idx) in vList" :key="idx">
                                        <div class="video_box" @click="bindOpenVideo(val.id)">
                                                <img
                                                        class="i"
                                                        alt=""
                                                        src="@/assets/images/page/videoPoster2.png"
                                                />
                                                <span class="play"
                                                        ><i class="iconfont icon-xiangxia"></i
                                                ></span>
                                                <focusText
                                                        :context="val[`title-en-us`]"
                                                        :keyWords="val.Key"
                                                ></focusText>
                                        </div>
                                        <div class="item-txt">
                                                <p class="title">{{ val[`title-en-us`] }}</p>
                                        </div>
                                </div>
                        </div>
                        <div class="tips" v-else>
                                <img class="empty" src="@/assets/images/common/empty.png" />{{
                                        t('User.tips3')
                                }}
                        </div>
                </div>
                <publicMaskPopup ref="iframeRef" isCenter @clickMask="bindMaskClick">
                        <template #slot_mask>
                                <div class="iframeBox">
                                        <div id="youtubePlayer"></div>
                                        <div class="err">
                                                <i
                                                        class="iconfont icon-guanbi"
                                                        @click="iframeRef.maskHide"
                                                ></i>
                                                <p class="text">{{ t('Education.errTips') }}</p>
                                        </div>
                                </div>
                        </template>
                </publicMaskPopup>
        </div>
</template>
<script setup>
import { ga4 } from '@/utils/ga4'
import { computed, onMounted, ref } from 'vue'
import { VIDEO_LIST, GET_VIDEO_LIST } from '@/data/video'
import { useRoute } from 'vue-router'
import videoBanner from './components/videoBanner.vue'
import focusText from './components/focusText.vue'
import youTubePlayer from 'youtube-player'
import { debounce } from '@/utils/tools'
import { ACADEMY_LIST } from '@/data/academy'

// definePageMeta({
//         layout: "academy",
//         index: 2
// });
onMounted(() => {
        ga4({
                types: 'first_visit',
                params: {
                        page_name: 'Educational Videos',
                },
                isDailyVisit: true,
        })
})

const iframeRef = ref()
const player = ref()
let inputVal = ref('')
const router = useRoute()
const activeTab = router.query.key ? ref(+router.query.key - 2) : ref(VIDEO_LIST.level1.key)
const vList = ref([])
const { t, locale } = useI18n()
const param = ref({
        level: 1,
        page: 1,
        limit: 999,
})
const videoData = ref({})

let item = ref([
        {
                path: '/academy',
                page: 'User.tips7',
        },
])
// 麵包屑二級導航
const setBreadcrumb = (page) => {
        if (item.value.length > 1) {
                item.value.splice(1, 1, {
                        path: '',
                        page,
                })
        } else {
                item.value.push({
                        path: '',
                        page,
                })
        }
}
//渲染視頻列表
const renderVideos = (key) => {
        param.value.level = key
        param.value.page = 1
        videoData.value = GET_VIDEO_LIST(param.value)
        vList.value = videoData.value.list
        setBreadcrumb(ACADEMY_LIST[key - 1].label)
}

watch(
        () => router,
        (newVal, OldVal) => {
                renderVideos(+newVal.query.key - 1)
        },
        { immediate: true },
)

const bindPageChange = (e) => {
        const media = window.innerWidth
        if (media > 1024) {
                param.value.page = e
        } else {
                param.value.limit += 4
        }
        videoData.value = GET_VIDEO_LIST(param.value)
        vList.value = videoData.value.list
}

const bindOpenVideo = (id) => {
        if (!player.value) {
                player.value = youTubePlayer('youtubePlayer', {
                        videoId: id,
                        width: '100%',
                        height: '100%',
                })
        }
        iframeRef.value.maskShow()
        player.value.playVideo()
}
const bindMaskClick = () => {
        player.value.destroy()
        player.value = null
}

const getTotalList = () => {
        let total = []
        for (let i = 1; i < 4; i++) {
                total.push(...VIDEO_LIST[`level${i}`].videos)
        }
        return total
}

const searchHandler = debounce(() => {
        const storeParams = JSON.parse(JSON.stringify(param.value))
        vList.value = getTotalList().filter((val) =>
                val[`title-en-us`].toLowerCase().includes(inputVal.value.toLowerCase()),
        )
        if (!inputVal.value) {
                vList.value = GET_VIDEO_LIST(storeParams).list
        }
}, 500)

const triggerHandler = (key) => {
        inputVal.value = ''
        renderVideos(key + 1)
        activeTab.value = key
}
onBeforeRouteUpdate((to, from) => {
        inputVal.value = ''
        activeTab.value = +to.query.key - 2
        renderVideos(+to.query.key - 1)
})
</script>
<style scoped lang="scss" src="./index.scss"></style>
