export const GET_VIDEO_LIST = (...data) => {

	let level = data[0].level ? data[0].level : 1;
	let page = data[0].page ? data[0].page : 1;
	let limit = data[0].limit ? data[0].limit : 1;
	if (!VIDEO_LIST[`level${level}`]) {
		return { msg: '无效等级！', code: 404 }
	}

	let video = VIDEO_LIST[`level${level}`];
	video.total = video.videos.length;
	video.totalPage = Math.ceil(video.total / limit);

	const startIndex = (page - 1) * limit;
	const endIndex = page * limit;
	video.list = video.videos.slice(startIndex, endIndex)

	return video
};


